<h2 id="override-indicators">Override indicators <a class="header-anchor" href="#override-indicators">&#128279;</a></h2>
<p>Use the <code v-pre>indicators</code> slot to override default Bootstrap indicators.</p>
<p>This is a scoped slot, use <code v-pre>slot-scope=&quot;props&quot;</code> in Vue 2.5+, otherwise <code v-pre>scope=&quot;props&quot;</code>.</p>
<carousel-override-indicators ref="carousel-override-indicators"/>
