<h2 id="custom-icons">Custom icons <a class="header-anchor" href="#custom-icons">&#128279;</a></h2>
<p><strong>Note</strong>: Bootstrap 3 has only 2 sets of icon supported for carousel at present:</p>
<ul>
<li><code v-pre>glyphicon glyphicon-chevron-left</code> and <code v-pre>glyphicon glyphicon-chevron-right</code> (default)</li>
<li><code v-pre>icon-prev</code> and <code v-pre>icon-next</code></li>
</ul>
<p>Additional CSS may needed if you prefer other icons or font...</p>
<p>Here is an example using <code v-pre>glyphicon-arrow-left</code> and <code v-pre>glyphicon-arrow-right</code>.</p>
<carousel-icons ref="carousel-icons"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">carousel</span> <span class="hljs-attr">icon-control-left</span>=<span class="hljs-string">&quot;my-icon glyphicon glyphicon-arrow-left&quot;</span> <span class="hljs-attr">icon-control-right</span>=<span class="hljs-string">&quot;my-icon glyphicon glyphicon-arrow-right&quot;</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">slide</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">&quot;(slide, index) in slides&quot;</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">&quot;index&quot;</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">&quot;width: 100%;height: 400px;&quot;</span> <span class="hljs-attr">:style</span>=<span class="hljs-string">&quot;{background:index % 2 === 0 ? &#x27;#99a9bf&#x27; : &#x27;#d3dce6&#x27;}&quot;</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">&quot;carousel-caption&quot;</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">h3</span>&gt;</span>This is {{slide.title}}<span class="hljs-tag">&lt;/<span class="hljs-name">h3</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">slide</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">carousel</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">slides</span>: [
          {<span class="hljs-attr">title</span>: <span class="hljs-string">&#x27;Slide 1&#x27;</span>},
          {<span class="hljs-attr">title</span>: <span class="hljs-string">&#x27;Slide 2&#x27;</span>},
          {<span class="hljs-attr">title</span>: <span class="hljs-string">&#x27;Slide 3&#x27;</span>},
          {<span class="hljs-attr">title</span>: <span class="hljs-string">&#x27;Slide 4&#x27;</span>}
        ]
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-comment">/* Using custom icons may require some additional CSS declarations */</span>
<span class="hljs-selector-class">.carousel-control</span> <span class="hljs-selector-class">.my-icon</span> {
  <span class="hljs-attribute">position</span>: absolute;
  <span class="hljs-attribute">top</span>: <span class="hljs-number">50%</span>;
