<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference">&#128279;</a></h1>
<h2 id="carousel-1"><a href="https://github.com/uiv-lib/uiv/blob/0.x/src/components/carousel/Carousel.vue">Carousel</a> <a class="header-anchor" href="#carousel-1">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Number</td>
<td></td>
<td></td>
<td>The current slide index, use this to manual change slide index.</td>
</tr>
<tr>
<td><code v-pre>indicators</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Show / hide the indicators.</td>
</tr>
<tr>
<td><code v-pre>controls</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Show / hide the controls.</td>
</tr>
<tr>
<td><code v-pre>interval</code></td>
<td>Number</td>
<td>5000</td>
<td></td>
<td>Slides running interval time in ms. Use <code v-pre>0</code> to stop interval.</td>
</tr>
<tr>
<td><code v-pre>icon-control-left</code></td>
<td>String</td>
<td>glyphicon glyphicon-chevron-left</td>
<td></td>
<td>The left control icon font class.</td>
</tr>
<tr>
<td><code v-pre>icon-control-right</code></td>
<td>String</td>
<td>glyphicon glyphicon-chevron-right</td>
<td></td>
<td>The right control icon font class.</td>
</tr>
</tbody>
</table></div><h3 id="slots">Slots <a class="header-anchor" href="#slots">&#128279;</a></h3>
